<template>
  <form @submit="validarForm">
    <!-- ou @submit.prevent="salvaForm" -->
    <div class="row mt-5">
      <div class="col-sm-12">
        <div class="card shadow">
          <header class="card-header">
            <strong class="align-self-center"> Crie tarefas </strong>
          </header>
          <div class="card-body">
            <div class="row">
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3">
                <div class="row">
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data de criação {{ ViewModel.dataCriacao }}</label
                    >
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data ultima alteração
                      {{ ViewModel.dataUltimaAlteracao }}</label
                    >
                  </div>
                </div>
                <label> Campos com * são de preenchimento obrigatório </label>

                <div class="row">
                  <div class="col-sm-2 col-md-2 col-lg-2 col-xl-2">
                    <label>Referência </label>
                    <input
                      class="form-control"
                      type="numbers"
                      placeholder="Digite um valor"
                      v-model="ViewModel.value"
                      readonly
                    />
                  </div>

                  <div class="col-sm-10 col-md-5 col-lg-5 col-xl-5">
                    <label>* Atribuída para:</label>
                    <Multiselect
                      v-model="ViewModel.atribuidaPara"
                      placeholder="Selecione o usuário"
                      :options="optionsUsuarios"
                      :disabled="inputReadonly"
                      :required="true"
                    />
                  </div>
                  <div class="col-sm-12 col-md-5 col-lg-5 col-xl-5">
                    <label>* Cliente</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="ViewModel.cliente"
                      placeholder="Digite o nome do cliente"
                      required
                      :disabled="inputReadonly"
                    />
                  </div>

                  <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <label>* Descrição </label>
                    <textarea
                      class="form-control"
                      v-model="ViewModel.descricao"
                      :readonly="inputReadonly"
                      placeholder="Digite a decrição"
                      required
                      rows="5"
                    ></textarea>
                  </div>
                  <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <label>Deadline</label>
                    <input
                      type="date"
                      class="form-control"
                      v-model="ViewModel.deadline"
                      :readonly="inputReadonly"
                      required
                    />
                  </div>
                  <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <label> Tarefa concluida? </label><br />
                    <input
                      type="checkbox"
                      v-model="ViewModel.concluida"
                      :readonly="inputReadonly"
                    />
                  </div>
                </div>

                <div class="d-flex mt-3 buttonsActions">
                  <button
                    v-if="mostrarBtnSalvar"
                    class="btn btn-success"
                    type="submit"
                  >
                    Salvar
                  </button>
                  <input
                    type="button"
                    v-if="mostrarBtnSalvar"
                    class="btn btn-warning ml-2 mr-2"
                    @click="limpaForm()"
                    value="Limpar"
                  />

                  <button
                    class="btn btn-secondary"
                    type="reset"
                    @click="$router.push('/tarefas')"
                  >
                    Voltar
                  </button>
                </div>
              </div>
              <br /><br /><br />
            </div>
          </div>
        </div>
       
        <div v-if="tarefaId" class="card shadow mt-3">
          <header class="card-header">
            <strong class="align-self-center"> Chat da taféra</strong>
          </header>
          <div class="card-body">
            <p class="text-center">
              mensagens mais recentes <i class="fas fa-arrow-up"></i>
            </p>
            <div class="row">
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 chat">
                <div class="row">
                  <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div v-for="(column, index) in chatMensage" :key="index">
                      <template v-if="column.mensagemEnvida == true">
                        <!-- chat inviado  -->
                        <div class="d-flex justify-content-end">
                          <div class="container containerChat col-10">
                            <div class="d-flex justify-content-end mb-1">
                              <!-- <i class="fas fa-laugh-wink"></i> -->
                              <span class="time-right mr-3">
                                {{ column.user }}</span
                              >
                              <i class="fas fa-arrow-up"></i>
                            </div>
                            <div class="d-flex justify-content-end">
                              <p>{{ column.mensagem }}</p>
                            </div>

                            <span class="time-right">{{ column.data }}</span>
                          </div>
                        </div>

                        <!-- fim chat inviado  -->
                      </template>
                      <template v-if="column.mensagemEnvida == false">
                        <!-- chat recebido -->
                        <div class="container containerChat darker col-10">
                          <div class="d-flex justify-content-start mb-1">
                            <!-- <i class="fas fa-laugh-wink "></i> -->
                            <i class="fas fa-arrow-down mr-3"></i>
                            <span class="time-left">{{ column.user }}</span>
                          </div>
                          <p>{{ column.mensagem }}</p>

                          <span class="time-left">{{ column.data }}</span>
                        </div>
                        <!-- fim chat recebido -->
                      </template>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                <div class="row">
                  <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div class="input-group mb-3">
                      <textarea
                        v-model="mensagemChat"
                        rows="4"
                        class="form-control"
                        placeholder="Digite uma mensagem"
                      />
                      <div class="input-group-append">
                        <input
                          type="button"
                          @click="enviaMensagemChat"
                          class="input-group-text"
                          value="Enviar"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import { ref, onMounted } from "vue";
import Multiselect from "@vueform/multiselect";
import { v4 as uuidv4 } from "uuid";
import Swal from "sweetalert2";
import { /* useRouter, */ useRoute } from "vue-router";
import { useStore } from "vuex";
import servico from "../../requisicoes/servicos/usuarios/index";
import firebase from "firebase";
import { useRouter } from "vue-router";
// import Chat from "../../components/chat/Chat.vue" ainda nãoda pra usar esse componente aqui pq essas tarefas usam o id gerado no front e não o id do banco 

export default {
  components: { Multiselect },
  setup() {
    const {
      params: { tarefaId, visualizar },
    } = useRoute();
    const store = useStore();
    const getId = () => {
      return tarefaId ? tarefaId : store.state.emptyGuid;
    };


    let mostrarBtnSalvar;
    let inputReadonly;
    let tarefaRef = Math.floor(Date.now() * Math.random()).toString(36);
    let usuariosBanco = ref();
    let router = useRouter();
    // let router = useRouter();
    let optionsUsuarios = ref([]);
    if (visualizar) {
      mostrarBtnSalvar = false;
      inputReadonly = true;
    } else {
      mostrarBtnSalvar = true;
      inputReadonly = false;
    }
    // if (!tarefaId) {
    // let dataLocalStorageTarefa = localStorage.getItem(
    //   "dataLocalStorageTarefa"
    // );
    // if (dataLocalStorageTarefa) {
    //   let tarefas = JSON.parse(dataLocalStorageTarefa);
    //   tarefas.forEach((tarefa) => {
    //     if (tarefa.value >= tarefaRef) {
    //       tarefaRef = tarefa.value + 1;
    //     }
    //   });
    // }
    // }

    let ViewModel = ref({
      id: ref(getId),
      value: ref(tarefaRef),
      atribuidaPara: ref(),
      lablAtribuidaPara: ref(),
      descricao: ref(),
      idBanco: ref(),
      concluida: ref(false),
      deadline: ref(),
      chat: ref([]),
      cliente: ref(""),
    });
    let mensagemChat = ref();
    let chatMensage = ref();

    const limpaForm = () => {
      ViewModel.value.id = ref();
      ViewModel.value.value = ViewModel.value.value + 1;
      ViewModel.value.descricao = ref();
      ViewModel.value.atribuidaPara = ref();
      ViewModel.value.concluida = ref(false);
      ViewModel.value.deadline = ref();
      ViewModel.value.dataCriacao = ref();
      ViewModel.value.dataUltimaAlteracao = ref();
    };

    const validarForm = (e) => {
      e.preventDefault();
      if (ViewModel.value.atribuidaPara) {
        salvaForm();
      } else {
        Swal.fire({
          icon: "warning",
          title: `Atenção`,
          text: `Campos com * são de preenchimento obrigatório`,
        });
      }
    };
    const salvaForm = () => {
      var data = new Date();
      var dia = data.getDate();
      var mes = data.getMonth();
      let ano = data.getFullYear();
      var hora = data.getHours();
      var min = data.getMinutes();
      if (tarefaId) {
        let data = {
          id: ViewModel.value.id,
          // value: ViewModel.value.value,
          atribuidaPara: ViewModel.value.atribuidaPara,
          lablAtribuidaPara: filtraLabelUsuario(),
          descricao: ViewModel.value.descricao,
          concluida: ViewModel.value.concluida,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          cliente: ViewModel.value.cliente,
        };
        let url = "tarefa/" + ViewModel.value.idBanco;
        servico
          .edit(url, data)
          .then(() => {
            Swal.fire({
              icon: "success",
              title: `Concluído`,
              text: `O item "${ViewModel.value.value}" foi editado `,
              showDenyButton: true,
              confirmButtonText: "Voltar",
              showCancelButton: true,
            }).then((result) => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                router.push("/tarefas");
              }
            });
          })
          .catch((error) => {
            console.error(error);
          });
      } else {
        ViewModel.value.id = uuidv4();
        let data = {
          id: ViewModel.value.id,
          value: ViewModel.value.value,
          atribuidaPara: ViewModel.value.atribuidaPara,
          lablAtribuidaPara: filtraLabelUsuario(),
          descricao: ViewModel.value.descricao,
          concluida: ViewModel.value.concluida,
          deadline: ViewModel.value.deadline,
          dataCriacao: dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          cliente: ViewModel.value.cliente,
        };
        console.log("data", data);
        servico
          .add("tarefa", data)
          .then(() => {
            Swal.fire({
              icon: "success",
              title: `Concluído`,
              text: `O item "${ViewModel.value.value}" foi adicionado `,
              showDenyButton: true,

              confirmButtonText: "Voltar",
              denyButtonText: `Adicionar mais taréfas`,
            }).then((result) => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                router.push("/tarefas");
              } else if (result.isDenied) {
                limpaForm();
              }
            });
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };

    const filtraLabelUsuario = () => {
      let label = "";
      for (var [key, value] of Object.entries(usuariosBanco.value)) {
        console.log("#########", key, value);
        if (value.id == ViewModel.value.atribuidaPara) {
          console.log("label is");
          label = value.nome;
        }
      }
      return label;
    };

    const obterChat = () => {
      let contador = 0;

      let url = "tarefa/" + ViewModel.value.idBanco;
      let chats = [];
      servico
        .get(url)
        .then((snapshot) => {
          console.log("snapshot", snapshot.val());
          if (snapshot.exists()) {
            // console.log(snapshot.val());
            let data = snapshot.val();
            // console.table(data);
            let userName = "";
            let resp = firebase.auth();
            if (resp) {
              userName = resp.currentUser.email;
            }

            for (var [key, value] of Object.entries(data.chat)) {
              console.log(value, key);
              let obj;
              if (userName == value.user) {
                obj = {
                  mensagem: value.mensagem,
                  user: value.user,
                  mensagemEnvida: true,
                  data: value.data,
                };
              } else {
                obj = {
                  mensagem: value.mensagem,
                  user: value.user,
                  mensagemEnvida: false,
                  data: value.data,
                };
              }

              chats.push(obj);
            }
            chats.reverse();
            chatMensage.value = chats;
            console.log("chats", chats);
          } else {
            contador++;
            if (contador <= 2) {
              setTimeout(function () {
                obterChat();
              }, 1000);
            }
            console.log("No data available");
          }
        })
        .catch((error) => {
          console.error(error);
        });
    };
    const enviaMensagemChat = () => {
      if (mensagemChat.value) {
        let userName = "";
        let resp = firebase.auth();
        if (resp) {
          userName = resp.currentUser.email;
        }
        console.log("mensagemChat", mensagemChat.value);
        console.log("userName", userName);

        var date = new Date();
        var dia = date.getDate();
        var mes = date.getMonth();
        let ano = date.getFullYear();
        var hora = date.getHours();
        var min = date.getMinutes();

        let data = {
          mensagem: mensagemChat.value,
          user: userName,
          data: dia + "/" + mes + "/" + ano + " :" + hora + ":" + min,
        };
        let url = "tarefa/" + ViewModel.value.idBanco + "/chat";
        servico
          .add(url, data)
          .then(() => {
            console.log("atualizar o chat");
            obterChat();
            mensagemChat.value = "";
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };

    onMounted(async () => {
      const obterSeletorUsuarios = () => {
        servico.get("usuarios").then((snapshot) => {
          if (snapshot.exists()) {
            let data = snapshot.val();
            console.log("data", data);
            usuariosBanco.value = data; // salva resp para não precisar fazer a requi de novo
            for (var [key, value] of Object.entries(data)) {
              console.log(key, value);
              let Usuario = {
                value: value.id,
                label: value.nome,
              };
              optionsUsuarios.value.push(Usuario);
            }
          }
        });

        // let dataLocalStorageUsuarios = localStorage.getItem(
        //   "dataLocalStorageUsuario"
        // );
        // if (dataLocalStorageUsuarios.length >= 1) {
        //   let localStorege = JSON.parse(dataLocalStorageUsuarios);
        //   localStorege.forEach((usuario) => {
        //     let Usuario = {
        //       value: usuario.id,
        //       label: usuario.nome,
        //     };
        //     optionsUsuarios.value.push(Usuario);
        //   });
        // } else {
        //   Swal.fire({
        //     icon: "warning",
        //     title: `Atenção`,
        //     text: `Você ainda não possui nenhum usuario cadastrado!
        //     Deseja cadastras?`,
        //     showDenyButton: true,
        //     confirmButtonText: "Sim",
        //     denyButtonText: "Não",
        //   }).then((result) => {
        //     if (result.isConfirmed) {
        //       router.push("/usuario/novo");
        //     } else if (result.isDenied) {
        //       console.log("continuar cadastrando");
        //     }
        //   });
        // }
      };
      obterSeletorUsuarios();
      const ObterTarefa = async (id) => {
        console.log("id", id);
        servico
          .get("tarefa")
          .then((snapshot) => {
            if (snapshot.exists()) {
              // console.log(snapshot.val());
              let data = snapshot.val();
              // console.table(data);
              for (var [key, value] of Object.entries(data)) {
                if (id == value.id) {
                  ViewModel.value.id = value.id;
                  ViewModel.value.atribuidaPara = value.atribuidaPara;
                  ViewModel.value.cliente = value.cliente;
                  ViewModel.value.descricao = value.descricao;
                  ViewModel.value.value = value.value;
                  ViewModel.value.idBanco = key;
                  ViewModel.value.concluida = value.concluida;
                  ViewModel.value.deadline = value.deadline;
                  ViewModel.value.dataCriacao = value.dataCriacao;
                  ViewModel.value.dataUltimaAlteracao =
                    value.dataUltimaAlteracao;
                  ViewModel.value.dataUltimaAlteracao =
                    value.dataUltimaAlteracao;
                }
              }
            } else {
              console.log("No data available");
            }
          })
          .catch((error) => {
            console.error(error);
          });
      };
      try {
        if (tarefaId !== undefined) {
          ObterTarefa(tarefaId);
          console.log("obterChat");
          obterChat();
        }
      } catch (err) {
        console.error("Erro ao obter usuario para edição", err);
      }
    });
    return {
      ViewModel,
      optionsUsuarios,
      limpaForm,
      validarForm,
      filtraLabelUsuario,
      mostrarBtnSalvar,
      inputReadonly,
      tarefaId,
      enviaMensagemChat,
      mensagemChat,
      obterChat,
      chatMensage,
    };
  },
};
</script>
<style src="@vueform/multiselect/themes/default.css"></style>
    
<style>
.chat {
  max-height: 400px;
  overflow-y: scroll;
}
.containerChat {
  border: 2px solid #dedede;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 10px;
  margin: 10px 0;
}
/* Darker chat container */

.darker {
  border-color: #ccc;
  background-color: #ddd;
}
/* Clear floats */

.container::after {
  content: "";
  clear: both;
  display: table;
}
/* Style images */

.container img {
  float: left;
  max-width: 60px;
  width: 100%;
  margin-right: 20px;
  border-radius: 50%;
}
/* Style the right image */

.container img.right {
  float: right;
  margin-left: 20px;
  margin-right: 0;
}
/* Style time text */

.time-right {
  float: right;
  color: #aaa;
}
/* Style time text */

.time-left {
  float: left;
  color: #999;
}
</style>
